import React from "react";
import preview from "../assets/preview.PNG";

/**
 * Tietoa-sivu. Kutsu App.js tiedostossa.
 */

export default function InfoPage() {
    return (
        <div className="infopage">
            <div className="infodiv">
                <h1>Tienhoito</h1>
                <p>
                    Sivusto visualisoi Digitrafficin kunnossapitodataa kartalle.
                    Sivustolla net harjaus-, auraus- ja hiekoituskaluston
                    suorittamat reitit sek kaluston sijainnin. Sivusto
                    mahdollistaa mys muiden reittien ja ajoneuvojen
                    suodattamisen tarkemman tehtvnimikeen mukaan. <br />
                    Data on perisin{" "}
                    <a
                        rel="noreferrer"
                        target="_blank"
                        href="https://www.digitraffic.fi/tieliikenne/#maanteiden-kunnossapitotiedot"
                    >
                        Digitrafficin rajapinnasta
                    </a>
                    . Sivusto hydynt{" "}
                    <a
                        rel="noreferrer"
                        target="_blank"
                        href="https://www.openstreetmap.org/"
                    >
                        OpenStreetMapsin
                    </a>{" "}
                    karttaa. Sovellus on toteutettu{" "}
                    <a
                        rel="noreferrer"
                        target="_blank"
                        href="https://react.dev/"
                    >
                        ReactJS:ll
                    </a>
                    ,{" "}
                    <a
                        rel="noreferrer"
                        target="_blank"
                        href="https://react-leaflet.js.org/"
                    >
                        React Leafletill
                    </a>{" "}
                    ja{" "}
                    <a
                        rel="noreferrer"
                        target="_blank"
                        href="https://leafletjs.com/"
                    >
                        Leafletill
                    </a>
                    .
                </p>
                <img className="preview" alt="kuvakaappaus sovelluksesta" src={preview}></img>
            </div>
        </div>
    );
}
