import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import { HashRouter } from "react-router-dom";

/**
 *Sovelluksen juuritiedosto. Kytt react-router-domin HashRouteria, sill kytettvll julkaisualustalla ei ole vapaasti mritettvi polkuja.
 * React.StrictMode kehityst ja bugien lytmist tukevana asetuksena.
 */

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
    <React.StrictMode>
        <HashRouter>
            <App />
        </HashRouter>
    </React.StrictMode>,
);
